body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.spinner-custom{
  display: -webkit-flex!important;
  display: flex!important;
  margin:30px auto;
}

.pt-popover .pt-popover-content{
  max-height:200px;
  overflow-y: auto;
}

.NonIdealState{
  margin-top:40px;
}

.currencyList{
  margin: 0;
  padding: 0;
  display: grid;
  grid-gap: 1px;
  grid: repeat(5, 15rem) / repeat(1, 15rem)
}

.popover-list {
  max-height: 250px;
  overflow: auto;
}


@media (min-width:1300px){
  .currencyList{
    margin: 0;
    padding: 0;
    display: grid;
    grid-gap: 1px;
    grid: repeat(5, 15rem) / repeat(5, 15rem)
  }
}

@media (min-width:992px)  and (max-width:1299px){
  .currencyList{
    margin: 0;
    padding: 0;
    display: grid;
    grid-gap: 1px;
    grid: repeat(5, 15rem) / repeat(3, 15rem)
  }
}

@media (min-width:600px) and (max-width:991px){
  .currencyList{
    margin: 0;
    padding: 0;
    display: grid;
    grid-gap: 1px;
    grid: repeat(5, 15rem) / repeat(2, 15rem)
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

